@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.accordion-container{width:98%;margin:20px auto 0px}.accordion-header{display:flex;justify-content:center;font-size:2rem;letter-spacing:1px;margin:1vw 0vw;width:100%;font-weight:bolder}.MuiAccordionSummary-root{background-color:rgba(192,193,196,0.9) !important}

.form-container{width:70%;margin:15px auto;min-height:95vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;box-shadow:10px 10px 10px;display:flex;flex-direction:column;align-items:center;font-family:'Open Sans Condensed';border:1px solid;border-radius:10px;background-color:rgba(228,226,233,0.9)}.form-container .errorMessage{color:#f36262;font-size:larger;font-weight:bold}.form-container .add-new-event-form{width:75%}.form-container .form-button{display:flex;height:100px;justify-content:space-between;margin:20px 0px;align-items:flex-end}

.group{position:relative;margin:40px 0}.group input[type='password']{letter-spacing:0.3em}.group .form-input{width:100%;display:block;background:none;color:#000;font-size:1rem;border:none;border-radius:0;border-bottom:1px solid grey;padding:10px  5px;margin:25px 0}.group .form-input.error{border-bottom:2px solid red}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:.9rem;color:grey}.group .form-input-label{color:#000;font-size:1.2rem;font-weight:bolder;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:.9rem;color:grey}

.custom-button{min-width:20vw;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;border-radius:4px;background-color:#727171;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{-webkit-filter:brightness(1.1);filter:brightness(1.1)}.custom-button:active{transform:scale(0.9)}@media screen and (max-width: 800px){.custom-button{min-width:15vw;padding:0 5px 0 5px}}.custom-button.disabled-button{background-color:rgba(114,113,113,0.9)}.custom-button.disabled-button:hover{cursor:not-allowed;-webkit-filter:brightness(1);filter:brightness(1)}.custom-button.disabled-button:active{transform:scale(1)}.custom-button.disabled-button:focus{outline:none}

.no-events{display:flex;width:100%;height:200px;justify-content:center;align-items:center;font-family:'Open Sans Condensed';font-size:3rem;font-weight:bolder;color:cornflowerblue}.filter-event-container{display:flex;flex-direction:column;justify-content:space-around;align-items:center;width:100%;min-height:200px;height:auto;background:#f8f8f8;padding-top:20px}.event-container{display:flex;width:100%;flex-wrap:wrap;justify-content:space-around;align-items:center;margin-top:20px}

.each-event-container{display:flex;width:calc(50% - 30px);min-width:350px;margin:20px 0px;flex-direction:column;justify-content:space-around;align-items:center;min-height:200px;height:auto;border:1px solid black;border-radius:5px;box-shadow:10px 10px 10px gainsboro;font-family:'Karla', sans-serif;font-size:1rem}.event-item{width:90%;padding:15px 0px;display:flex;justify-content:space-between}

.select-css{font-family:'Open Sans Condensed';font-size:14px;color:#444;line-height:1.3;padding:.6em 1.4em .5em .8em;width:150px;max-width:100%;max-height:35px;box-sizing:border-box;margin:0;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:.5em;-moz-appearance:none;-webkit-appearance:none;appearance:none;background-color:#fff;background-image:url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),linear-gradient(to bottom, #fff 0%, #e5e5e5 100%);background-repeat:no-repeat, repeat;background-position:right .7em top 50%, 0 0;background-size:.65em auto, 100%}@media screen and (max-width: 800px){.select-css{width:80px;height:35px;font-size:12px}}@media screen and (max-width: 400px){.select-css{width:60px;height:35px;font-size:12px}}.select-css::-ms-expand{display:none}.select-css:hover{border-color:#888}.select-css:focus{border-color:#aaa;box-shadow:0 0 1px 3px rgba(170,169,169,0.7);box-shadow:0 0 0 3px -moz-mac-focusring;color:#222;outline:none}.select-css option{font-weight:normal}

.spinnercontainer{height:360px;width:100%;display:flex;justify-content:center}.spinnercontainer .spinnerOverlay{display:inline-block;margin-top:150px;width:50px;height:50px;border:3px solid rgba(195,195,195,0.6);border-radius:50%;border-top-color:#636767;animation:spin 1s ease-in-out infinite;-webkit-animation:spin 1s ease-in-out infinite}@keyframes spin{to{-webkit-transform:rotate(360deg)}}@-webkit-keyframes spin{to{-webkit-transform:rotate(360deg)}}

